<template>
    <Toast :isActive="isShowToast" :message="toastMsg" class="toast" @close="toastClose"></Toast>
    <navgation :navititle="currentNavTitle"></navgation>
    <div class="container">
        <div class="pdf-tool-top-container">
            <img @click="zoomOut" class="zoom-btn" src="../assets/zoomIn.png"></img>
            <img @click="zoomIn" class="zoom-btn" src="../assets/zoomOut.png"></img>
            <img @click="pdfPrint" class="print-btn" src="../assets/print.png"></img>
            <img @click="PDFDownload" class="download-btn" src="../assets/download.png"></img>
        </div>
        <div class="pdf-container">
            <div class="pdf-content-container" :style="{
                transform: `scale(${scaleData})`,
            }" @touchstart="handleTouchStart" @touchend="handleTouchEnd">
                <!-- <vue-office-docx class="docx-content" :src="wordUrl" @rendered="rendered">
            </vue-office-docx> -->
                <VuePdfEmbed :source="pdfUrl" :page="curPage" class="pdf-content" @loaded="pdfLoaded" ref="vuePdfRef">
                </VuePdfEmbed>
            </div>
        </div>
        <div class="pdf-tool-container">
            <img class="preview-btn" @click="previewShow" src="../assets/preview.png"></img>
            <div @click="pageTop" class="page-turning-btn-top"></div>
            <div @click="pageLeft" class="page-turning-btn-left"></div>
            <div class="pageNumber">{{ + curPage + '/' + totalPageNum }}</div>
            <div @click="pageRight" class="page-turning-btn-right"></div>
            <div @click="pageEnd" class="page-turning-btn-end"></div>
            <img class="quick-back" @click="quickBack" src="../assets/quickBack.png"></img>
            <!-- <div>{{ ' ' + curPage + '/' + totalPageNum + ' ' }}</div>
            <div class="page-op-container">
                <input type="text" v-model="toPageNum" placeholder="" class="page-op-input">
                <button class="page-op-confirm" @click="pageConfirm"></button>
                <button class="preview-btn" @click="previewShow"></button>
            </div>
            <div class="page-turning-container">
                <div @click="pageTop" class="page-turning-btn-top"></div>
                <div @click="pageLeft" class="page-turning-btn-left"></div>
                <div @click="pageRight" class="page-turning-btn-right"></div>
                <div @click="pageEnd" class="page-turning-btn-end"></div>
            </div>
            <div>
                <button @click="zoomIn"></button>
                <button @click="zoomOut"></button>
            </div>
            <button class="print-btn" @click="pdfPrint">pdf</button>
            <button class="print-btn" @click="PDFDownload">pdf</button> -->
        </div>
        <!-- <iframe src="" frameborder="0"></iframe> -->
        <transition name="preview-fade">
            <div class="preview-container" v-if="previewShowFlag">
                <div class="preview-item" v-for="index in totalPageNum" :key="index" @click="previewClick(index)">
                    <!-- <div class="preview-pdf-item"></div> -->
                    <VuePdfEmbed :source="pdfUrl" :page="index" class="preview-pdf-item">
                    </VuePdfEmbed>
                    <div class="preview-item-index">{{ index }}</div>
                </div>
            </div>
        </transition>

    </div>
</template>

<script setup>
//toast
import Toast from '@/components/Toast.vue'
const toastMsg = ref('')
const isShowToast = ref(false)
function toastClose(params) {
    isShowToast.value = false
    console.log(params)
}


import { ref, computed, onMounted, nextTick, reactive } from 'vue'
import VuePdfEmbed from "vue-pdf-embed"
import VueOfficeDocx from '@vue-office/docx';
import navgation from '@/components/navigation.vue'
import '@vue-office/docx/lib/index.css'
import { useRoute, useRouter } from "vue-router"

const router = useRouter()
const route = useRoute()
const fileInfo = route.query
const currentNavTitle = ref('')
currentNavTitle.value = fileInfo.name
const countObj = ref({ count: 0 })
const vuePdfRef = ref(null)
const curPage = ref(1)
const totalPageNum = ref(0)

const wordUrl = ref('/testWord1.docx')

import { useFileStore } from "@/stores/file";
const fileStore = useFileStore()

const pdfUrl = ref('/testPdf.pdf')
pdfUrl.value = fileInfo.link

// pdfUrl.value = ref(linkUrl)
// pdfUrl.value = fileStore.file.fileList[fileStore.file.index].link
// const pdfSource = reactive({
//     url: '/testPdf.pdf',
//     // url: 'http://112.124.24.166:81/static/.pdf',
//     cMapUrl: 'https://cdn.jsdelivr.net/npm/pdfjs-dist@2.9.359/cmaps/',
//     cMapPacked: true,
// })
const toPageNum = ref(0)
const scaleData = ref(1.0);
const pageHeight = ref('100%');
const previewShowFlag = ref(false)
let startX = 0;
let startY = 0;

function rendered(err) {
    console.log('word rendered')
}

// onMounted(() => {
//     const loadingTask = createLoadingTask(pdfUrl.value)
//     loadingTask.promise.then((pdf) => {
//         totalPageNum.value = pdf.numPages
//     }).finally(() => {
//         // setTimeout(() => {
//         //     // refreshKey.value += 1
//         //     // refresh.value = true
//         //     nextTick(() => {
//         //         refresh.value = true
//         //     });
//         // }, 1000);

//     })
//     // setTimeout(() => {
//     //     pdfUrl.value = '/testPdf.pdf'
//     // }, 500);
// })

function zoomIn() {
    scaleData.value += 0.1;
    pageHeight.value = (parseInt(pageHeight.value) - 5.0) + '%';
}

function zoomOut() {
    scaleData.value -= 0.1;
    pageHeight.value = (parseInt(pageHeight.value) + 5.0) + '%';
}

function pdfLoaded(pdf) {
    totalPageNum.value = pdf.numPages
}

function increment() {
    countObj.value.count++
    addArrayItem(countObj.value.count)
}

function pageConfirm() {
    if (toPageNum.value > 0 && toPageNum.value <= totalPageNum.value) {
        curPage.value = Number(toPageNum.value)
    } else {
        isShowToast.value = true
        toastMsg.value = ''
    }
}

function previewClick(index) {
    curPage.value = index
    previewShowFlag.value = false
}

function previewShow() {
    previewShowFlag.value = !previewShowFlag.value
}

function quickBack(params) {
    router.back()
}

function handleTouchStart(event) {
    if (scaleData.value != 1) {
        return
    }
    startX = event.touches[0].clientX;
    startY = event.touches[0].clientY;
}

function handleTouchEnd(event) {
    if (scaleData.value != 1) {
        return
    }
    const endX = event.changedTouches[0].clientX;
    const endY = event.changedTouches[0].clientY;
    const gapX = endX - startX;
    const gapY = endY - startY;
    if (Math.abs(gapX) > Math.abs(gapY)) {
        if (gapX > 50) {
            pageLeft();
        } else if (gapX < -50) {
            pageRight()
        }
    }
}

function pageTop() {
    curPage.value = 1
}

function pageLeft() {
    let curPageValue = curPage.value - 1
    if (curPageValue <= 0) {
        isShowToast.value = true
        toastMsg.value = ''
        // nextTick(() => {
        //     isShowToast.value = false
        // })
    } else {
        curPage.value = curPageValue
    }

}

function pageRight() {
    let curPageValue = curPage.value + 1
    if (curPageValue > totalPageNum.value) {
        isShowToast.value = true
        toastMsg.value = ''
        // nextTick(() => {
        //     isShowToast.value = false
        // })
    } else {
        curPage.value = curPageValue
    }

}

function pageEnd() {
    curPage.value = totalPageNum.value

}

function pdfPrint() {
    vuePdfRef.value?.print(200, 'text1', true)
}

function getPDFFileName() {
    // const last_index = pdfSource.url.lastIndexOf('/');
    // return pdfSource.url.slice(last_index + 1);
    const pdfUrlString = fileInfo.link
    pdfUrl.value = "http://112.124.24.166:81/static/.pdf"
    pdfUrl.value = fileInfo.link
    console.log("----------" + typeof pdfUrl.value);    //  'string'
    console.log("----------" + pdfUrl.value);           //  '/testPdf.pdf'
    const last_index = pdfUrl.value.lastIndexOf('/');
    return pdfUrlString.slice(last_index + 1);
}

// -- pdf --
function PDFDownload() {
    vuePdfRef.value?.download(getPDFFileName());
}

</script>

